@font-face {
    font-family: 'IBM Plex Sans';
    src: url('fonts/subset-IBMPlexSans-Regular.woff2') format('woff2'),
        url('fonts/subset-IBMPlexSans-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

html {
  margin: 0;
}

body {
  padding: 20px 0 0 40px;
}

* {
  box-sizing: border-box;
  font-family: IBM Plex Sans, sans-serif;
}

.title {
  font-size: 26px;
  font-weight: 400;
  letter-spacing: -0.02em;
}

.paragraph {
  font-size: 18px;
  max-width: 400px;
}

.list {
  padding: 0;
  list-style-position: inside;
  list-style-type: none;
  font-size: 18px;
}

.list__title {
  font-size: 18px;
  font-weight: 600;
}

.list__item {
  margin-left: 20px;
}

.youtube { max-width: 656px; width: 100%; }

.githubLink { display: flex; }
.githubLink__image { width: 28px; height: 28px; margin-right: 10px;}
.githubLink__title { font-size: 24px; color: #3247B1; }
